@import "./NavUlLi";

.subNav{
  display: flex; 
  height: 31px;
  width: 100%;
  background-color: #FFFFFF;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
  justify-content: center;
  @include NavUlLi(1280px, $font-color-gray2, $font-color-black, #FFFFFF);
  li{
    position: relative;
    &:hover{
      div{
        background-color: $brand-color-yellow;
      }
    }
    div{
      position: absolute;
      bottom: -2px;
      width: 100%;
      height: 3px;
    }
  }
}