@mixin NavUlLi($width, $color, $hoverColor, $hoverBackgroundcolor){
  ul{
    display: flex;
    height: 100%;
    width: $width;
  }
  li{
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $color;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 14px;
    font-weight: 400;
    cursor: pointer;
    &:hover{
      font-weight: 500;
      color: $hoverColor;
      background-color: $hoverBackgroundcolor;
    }
  }
}